window.jquery = function(Operator) {
  let elements;
  if (typeof Operator === 'string') {
    elements = document.querySelectorAll(Operator);
  } else if (Operator instanceof Array) {
    elements = Operator;
  }
  return {
    print() {
      console.log(elements);
    },
    each(fn) {
      for (let i = 0; i < elements.length; i++) {
        fn.call(null, elements[i], i);
      }
    },
    find(selectStr) {
      let List = [];
      //è¿™é‡Œçš„thisï¼š
      //{print: Æ’, each: Æ’, find: Æ’, addClass: Æ’, removeClass: Æ’,Â â€¦}
      //æ˜¯jqueryè¿”å›žçš„ä¸€ä¸ªå¯æ“ä½œå¯¹è±¡
      //elementsæ˜¯è¢«jqueryæ“ä½œçš„å¯¹è±¡
      List.oldJqueryElements = this;

      this.each(element => {
        List.push(...element.querySelectorAll(selectStr));
      });
      return jquery(List);
    },
    addClass(className) {
      this.each(node => {
        node.classList.add(className);
      });
      return this;
    },
    removeClass(className) {
      this.each(node => {
        node.classList.remove(className);
      });
      return this;
    },
    parent() {
      let par = [];
      this.each(node => {
        if (par.indexOf(node.parentNode) < 0) {
          par.push(node.parentNode);
        }
      });
      return jquery(par);
    },
    children() {
      let child = [];
      this.each(node => {
        child.push(...node.children);
      });
      return jquery(child);
    },
    end() {
      return this.oldJqueryElements;
    },
    oldJqueryElements: elements.oldJqueryElements
  };
};
window.$ = window.jquery;
